.container {
  --size-button: 40px;

  margin-top: 90px;
  position: fixed;
  top: -1.6rem;
  width: 100%;
  height: 10rem;
  background-image: url("../assets/images.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.input {
  padding-left: var(--size-button);
  height: var(--size-button);
  font-size: 15px;
  border: none;
  color: #fff;
  outline: none;
  width: var(--size-button);
  transition: all ease 0.3s;
  background-color: #191a1e;
  box-shadow:
    1.5px 1.5px 3px #0e0e0e,
    -1.5px -1.5px 3px rgb(95 94 94 / 25%),
    inset 0 0 0 #0e0e0e,
    inset 0 -0 0 #5f5e5e;
  border-radius: 50px;
  cursor: pointer;
  z-index: -100;
}

.input:focus,
.input:not(:invalid) {
  width: 200px;
  cursor: text;
  box-shadow:
    0 0 0 #0e0e0e,
    0 0 0 rgb(95 94 94 / 25%),
    inset 1.5px 1.5px 3px #0e0e0e,
    inset -1.5px -1.5px 3px #5f5e5e;
}

.input:focus + .icon,
.input:not(:invalid) + .icon {
  pointer-events: all;
  cursor: pointer;
}

.image {
  width: 100%;
  position: relative;
  top: -5rem;
  z-index: -1;
}

.grid {
  margin-top: 13.9rem;
  background-color: #0e0e0e;
  display: grid;
  grid-gap: 0.4rem;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  padding: 10px;
}
